import React, { Component, memo } from 'react';
import ReelProducts from './Elements/ReelProducts'
import ReelMedia from './Elements/ReelMedia'
import { Swiper, SwiperSlide } from 'swiper/react'
import ReelBranding from './Elements/ReelBranding';
import 'swiper/css';

const ReelBody = ({ wall, item, personalization, productLoading, showAddToCart, onsetProductImg, openAddtocart, setOnProductDetails, isFreePlan, index, currentVideo, muteToggle, muted }) => {
    const ugcProduct = item.ugc_products
    const autoPlay = (item.type === 3 || item.type === 5) && currentVideo ? true : false

    return (
        <>
            <ReelMedia itemData={item} wallID={wall.Wall.wallId} ownerId={wall.Wall.owner} ThemeID={personalization.widgetTheme} wall={wall} muted={muted} autoPlay={autoPlay} index={index} />
            <div className='tb_rp_content'>
                {wall.ProductSetting && personalization.themeType == 1 && ugcProduct && ugcProduct.length > 0 ?
                    <Swiper
                        autoHeight={true}
                        speed={400}
                        edgeSwipeDetection={true}
                        slidesPerView={item.ugc_products && item.ugc_products.length > 1 ? 1.3 : 1}
                        touchRatio={2}
                        spaceBetween={0}
                        mousewheel={{ forceToAxis: true, sensitivity: 2 }}
                        watchSlidesProgress={true}
                        direction='horizontal'
                        className="tb_rpp_wrap">
                        {item.ugc_products && item.ugc_products.map((product, index) => {
                            return <SwiperSlide key={index}>
                                <ReelProducts product={product} openAddtocart={openAddtocart} onsetProductImg={onsetProductImg} showAddToCart={showAddToCart} productLoading={productLoading} wall={wall} ugcSetting={wall.ProductSetting} item={item} setOnProductDetails={setOnProductDetails} />
                            </SwiperSlide>
                        })
                        }
                    </Swiper>
                    : null}


                {autoPlay ?
                    <div className="tb_rp_mute_btn" onClick={muteToggle}>
                        <div className={`tb__icon tb-${!muted ? `unmute` : `mute`}`}><div></div></div>
                    </div>
                    : null}

                {(wall.UserRule.branding_lite && wall.UserRule.branding_lite == 1) || isFreePlan ? <ReelBranding wallId={wall.Wall.id} themeID={wall.Personalization.widgetTheme} /> : null}
            </div>
        </>
    );
}
export default memo(ReelBody);