import React, { useState, memo } from 'react'
import { themePostTracking } from '../../../../actions/themeActions';
import { fetchShopifyProduct } from '../../../../actions/cartActions';

const ReelProducts = ({ product, wall, onsetProductImg, openAddtocart, item, setOnProductDetails }) => {
    const [noImg, setNoImg] = useState(false)
    const [loading, setLoading] = useState(false)
    const ownerId = wall.User.id;
    const Button = wall.ProductSetting.Button;
    const tracking = (wall, product) => themePostTracking({ type: 1, action: 2, wall: wall?.Wall?.id, product_id: product.id, post: item.referenceId, owner: wall.Wall.owner, feed: item.feedId, currency: product.price_currency, price: product.product_discount > 0 ? product.product_discount : product.product_price })
    const checkForShopify = async () => {
        setLoading(true)
        const itemDetails = {};
        if (product && product.UgcProduct && product.UgcProduct.product_url) {
            try {
                if (wall?.Wall?.owner == 140717 || wall?.ProductSetting?.Button?.pdp_target == 1) {
                    openUrl(product.UgcProduct.product_url)
                    onsetProductImg(product.product_image)
                    setLoading(false)
                    tracking(wall, product.UgcProduct)
                }
                else {
                    const isMagento = product.UgcProduct.created_from == "magento" || false
                    fetchShopifyProduct(product.UgcProduct.product_url, isMagento, { productId: product.UgcProduct.product_sku, storeId: product.UgcProduct.shopify_shop_id })
                        .then(res => {
                            if (isMagento && res.status == false) {
                                itemDetails[product.id] = {
                                    data: { available: true },
                                    showCart: wall?.Wall?.owner !== 140717
                                };
                            }
                            else {
                                const productData = isMagento && res?.data?.data || res?.data;
                                const formatProduct = isMagento ? { available: true } : {}
                                itemDetails[product.id] = {
                                    data: productData?.id ? { ...productData, ...formatProduct } : null,
                                    showCart: productData?.id && wall?.Wall?.owner !== 140717
                                };
                            }

                            onsetProductImg(res?.data?.featured_image ? res.data.featured_image : product.product_image)
                            setLoading(false)
                            setOnProductDetails(itemDetails)
                            openAddtocart(product.id, product)
                            // if (res.data) {
                            //     itemDetails[product.id] = res.data;
                            //     onsetProductImg(res?.data?.featured_image ? res.data.featured_image : product.product_image)
                            //     setLoading(false)
                            //     setOnProductDetails(itemDetails)
                            //     openAddtocart(product.id, product)
                            // }
                        })
                        .catch(error => {
                            console.log('error', error)
                            openUrl(product.UgcProduct.product_url)
                            onsetProductImg(product.product_image)
                            setLoading(false)
                            tracking(wall, product.UgcProduct)
                        });
                }


            } catch (error) {
                console.log('error', error)
                openUrl(product.UgcProduct.product_url)
                setLoading(false)

            }
        }
    }
    const openUrl = (url) => {
        const { Button } = wall.ProductSetting
        var newUrl = url;
        if (parseInt(Button.newTab) == 2) window.parent.location.href = newUrl
        else window.open(newUrl, '_blank');
    }

    const products = product.UgcProduct
    return <div className={`tb_rpp_pro ${noImg ? "ts_reelTheme_No_Product" : ""}`}>
        {!noImg ? <div className='tb_rpp_pro_img'>
            <img src={products.product_image} alt="" onError={() => setNoImg(true)} />
        </div> : null}
        <div className="tb_rpp_pro_cont">
            <div className="tb_rpp_pro_title">
                {products.product_title}
            </div>
            {products.product_price > 0 || products.product_discount > 0 ?
                <div className="tb_rpp_p_wrap">
                    <div className="tb_rpp_p">
                        {products.price_currency_symbol}{products.product_discount > 0 ? products.product_discount : products.product_price}
                    </div>
                    {products.product_discount == 0 || products.product_price == products.product_discount ? <></> :
                        <div className="tb_rpp_p tb_rpp_d_p">
                            {products.price_currency_symbol}{products.product_price}
                        </div>}
                </div> : null}
            <div className={`tb_rpp_btn${loading ? ' tb_rpp_btn_spinner' : ""}`} disabled={loading} onClick={() => { checkForShopify(); }}>{Button.text} </div>

        </div>
    </div>
}

export default memo(ReelProducts)