import React, { Component, Suspense, memo } from 'react'

const Image = React.lazy(() => import('../../../Themes/Elements/Media/Image'));
const ReelVideo = React.lazy(() => import('./ReelVideo'));
const ReelMediaSlider = React.lazy(() => import('./ReelMediaSlider'));

const ReelMedia = (props) => {
       const { itemData, wallID, ownerId, wall, muted, autoPlay, index } = props
       const size = false
       return <div className="tb_rp_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
              {
                     itemData.imageList && itemData.imageList.length > 0 ?
                            <Suspense fallback={<></>}><ReelMediaSlider imageList={itemData.imageList} {...props} /></Suspense>
                            : (itemData.type === 2 || itemData.type === 4 || !autoPlay) ?
                                   <Suspense fallback={<></>}>
                                          <Image ImageClass="tb_rp_image" data={{ ...itemData, postFileNew: itemData.filename }} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} />
                                   </Suspense>
                                   : (itemData.type === 3 || itemData.type === 5) ?
                                          <Suspense fallback={<></>}><ReelVideo VideoClass="tb_rp_video" data={{ ...itemData, postFileNew: itemData.filename }} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={autoPlay} muted={muted} handleVideoEnded={() => { }} index={index} /></Suspense>
                                          : null
              }
       </div>
}
export default memo(ReelMedia)





