import React, { Component, memo } from "react";
import { isValidAuthorImage, IMG_DATA_FILTER } from '../../../utils';
import { themePostTracking } from '../../../actions/themeActions'
import { isTagEmbed, suiteTrack } from "../../../actions/api";
import AddToCart from "./Elements/AddToCart";
import CheckOut from "./Elements/CheckOut";
import MagentoCart from '../CartPopUp/Magento'
import CartPopUp from '../CartPopUp'
import ReelBody from "./ReelBody";
import { connect } from "react-redux";
import { getDataNextSteps } from "../../../actions/themeActions";
import { register } from 'swiper/element/bundle';
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/css';

register()
class PostPopUp extends Component {
    constructor(props) {
        super(props);
        this.sliderRef = React.createRef();
        this.state = {
            currentIndex: this.props?.currentIndex,
            item: this.props.item,
            isVideoError: false,
            windowWidth: window.innerWidth,
            isAuthorImagevalid: true,
            instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link),
            isHotspot: false, addtoCartPopup: false,
            allProductDetails: null, selectedProductId: null,
            selectedProductDetails: null,
            continueShopping: false,
            selectedVariant: null,
            showAddToCart: false,
            productLoading: true,
            productImg: '',
            tagshopProductDeatil: null,
            muted: false,
            activeSlide: 0,
            addCartSetTime: false,
            selectedProdDetails: null, tagProdDetail: null
        }
        this.splideRef = React.createRef()
    }

    componentDidMount() {
        const { currentIndex, item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
        this.splideRef.current.addEventListener('swiperslidechange', (event) => {
            if (this.props.hasMoreData && event.detail[0].progress > 0.9) { this.requestData(); this.setState({ currentIndex: this.splideRef.current.swiper.activeIndex }) }
        })

    }

    setMuted = event => this.setState({ muted: !this.state.muted, })
    setOnProductDetails = (data) => this.setState({ allProductDetails: data })
    componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps; const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isVideoError: false, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
    }
    onErrorVideo = (e) => this.setState({ isVideoError: true })
    onMediaUpdate = (e) => this.setState({ isAuthorImagevalid: false });
    onAddToCart = (id, product, item) => {
        const { wall } = this.props;
        setTimeout(() => {
            this.setState({
                addCartSetTime: true
            })
        }, 100);
        const trackingData = {
            type: 1, action: 2,
            wall: wall.Wall.id,
            feed: item.feedId,
            post: item.referenceId ? item.referenceId : item.id,
            owner: wall.Wall.owner,
            product_id: product.UgcProduct.id,
            currency: product.UgcProduct.price_currency, price: product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price
        }
        this.setState({ addtoCartPopup: true, selectedProductDetails: this.state.allProductDetails[id], tagshopProductDeatil: { ...product, trackingData } })
    }


    requestData = () => {
        const { wall, appendData, hasMoreData, loaderData } = this.props;
        const postCount = wall.ThemeRule.numberOfPosts;
        const timeStamp = Math.floor(Date.now() / 1000);
        if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent)
    }

    onItemChange = (item) => this.setState({ item })

    currentSwiperSlide = e => {
        if (this.props.hasMoreData && e.progress > 0.9) { this.requestData(); }
        this.onPopUpTrack(e.activeIndex)
    }

    onPopUpTrack = (currentIndex) => {
        try {
            const { wall, completeData } = this.props
            let item = completeData[currentIndex] ? completeData[currentIndex] : null;
            if (item) {
                const requestData = { feed: item.feedId, post: item.referenceId ? item.referenceId : item.id }
                const tagembedPayload = { wallId: wall.Wall.id, userId: wall.Wall.owner, feedId: item.feedId, postId: item.id }
                const ugcPayload = { action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, type: 3, owner: wall.Wall.owner }
                themePostTracking(isTagEmbed ? tagembedPayload : suiteTrack ? ugcPayload : { action: 2, wall: wall.Wall.id, ...requestData, owner: wall.Wall.owner })
            }
        }
        catch (error) {
            console.log("Error in PopUp Tracking", error)
        }

    }

    startSwiperSlide = e => {
        this.setState({
            activeSlide: e.activeIndex
        })
    }
    onClickClosePopUp = (e) => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null, addCartSetTime: false })
    onClickCheckOut = (e) => this.setState({ addtoCartPopup: true, continueShopping: false })



    render() {
        const { personalization, onClosePopUp, slideWithArrowKeys, wall, webFilters, completeData, isFreePlan } = this.props;
        const { currentIndex, item, isVideoError, instaImg, addtoCartPopup, selectedProductDetails, continueShopping, selectedVariant, showAddToCart, productImg, productLoading, tagshopProductDeatil, activeSlide, addCartSetTime, muted, selectedProdDetails, tagProdDetail } = this.state;


        return <div className={`modalId${item.id} tb_rp_modal ${addCartSetTime ? 'tb_rp_add_cart_m' : ''}`} onKeyDown={slideWithArrowKeys} onKeyPress={slideWithArrowKeys} onLoad={slideWithArrowKeys} onClick={(e) => e.currentTarget == e.target && onClosePopUp(e)} >
            <div className="tb_rp_dialog">
                {!(addtoCartPopup || continueShopping) ?
                    <div className="tb_reel_close_btn" onClick={onClosePopUp}>
                        <div className='tb__icon tb-close-thin'><div></div></div>
                    </div> : null}

                <div className="tb_time_line_wrap">
                    <div className="tb_time_line"><div></div></div>
                </div>

                <Swiper
                    ref={this.splideRef}
                    direction="vertical"
                    touchRatio={2}
                    freeMode={JSON.stringify({ enabled: true, sticky: true })}
                    height={`${window.innerHeight}`}
                    initialSlide={currentIndex}
                    passiveListeners={true}
                    mousewheel={{ forceToAxis: true, sensitivity: 2 }}
                    watchSlidesProgress={true}
                    onSlideChange={swiper => this.currentSwiperSlide(swiper)}
                // onSwiper={(swiper) => this.startSwiperSlide(swiper)}
                >
                    {
                        (completeData && completeData.length > 0) ? completeData.map((item, index) => {
                            return <SwiperSlide key={`${item.id}-${index}`}>
                                {({ isActive }) => <ReelBody currentVideo={isActive} key={`${item.id}-${index}`} onClosePopUp={onClosePopUp} wall={wall} item={item} instaImg={instaImg} showAddToCart={showAddToCart} openAddtocart={(id, product) => this.onAddToCart(id, product, item)} onsetProductImg={(img) => this.setState({ productImg: img })} productLoading={productLoading} sliderRef={this.sliderRef} personalization={personalization} isVideoError={isVideoError} addtoCartPopup={addtoCartPopup} continueShopping={continueShopping} setOnProductDetails={this.setOnProductDetails} isFreePlan={isFreePlan} muteToggle={this.setMuted} muted={muted} />}
                            </SwiperSlide>
                        }) : <></>
                    }

                </Swiper>
            </div>

            {addtoCartPopup && selectedProductDetails ?
                <>
                    <div className="tb_rp_cart_overlay"><div></div></div>
                    <div className="tb_rp_cart_modal">
                        <div className="tb_rp_cart_body">
                            <div className="ts_reel_close_btn tb_reel_close_btn" onClick={addtoCartPopup && !continueShopping ? this.onClickClosePopUp : continueShopping ? event => this.setState({ addtoCartPopup: true, continueShopping: false }) : null}>
                                <div className='tb__icon tb-close-thin'><div></div></div>
                            </div>
                            <div className="tb_rp_cart_wrap">

                                {selectedProductDetails && Object.keys(selectedProductDetails).length > 0 ? tagshopProductDeatil?.UgcProduct?.created_from == "magento" ? <MagentoCart productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeCart={event => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.onClosePopUp} /> : <CartPopUp productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeCart={event => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} closePopUp={this.props.onClosePopUp} /> : null}
                                {/* {continueShopping ? <CheckOut productImg={productImg} wall={wall} addtoCartPopup={addtoCartPopup} productDetails={selectedVariant} tagshopProductDeatil={tagshopProductDeatil} onBack={() => this.setState({ addtoCartPopup: true, continueShopping: false })} closeAddtocart={() => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} />
                                    : <AddToCart productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeAddtocart={() => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} />} */}
                            </div>
                        </div>
                    </div>
                </>
                : null}
        </div>
    }
}

const mapStateToProps = state => {
    const { appData } = state;
    return {
        loaderData: state.loaderData,
        appendData: appData.postData.appendData,
        hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(memo(PostPopUp));